:root {
  --veryDarkColor: rgb(66, 52, 52);
  --darkColor: rgb(110, 87, 87);
  --lightBrown: rosybrown;
  --fontStack: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --background: linear-gradient(to top, #ff9a9e, #fad0c4);
}

body {
  background: var(--background);
}

.content {
  background: var(--background);
  padding: 20px;
  min-height: calc(100vh - 40px);
}

.switch-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 100px;
}

.switch {
  position: relative;
  height: 200px;
  width: 100px;
  background: var(--lightBrown);
  border-radius: 50px 0 0 50px;
  margin-right: 2px;
}

.switch:last-of-type {
  border-radius: 0 50px 50px 0;
  margin-right: 0;
  margin-left: 2px;
}

.switch::after {
  content: '';
  height: 50px;
  width: 50px;
  position: absolute;
  top: 25px;
  left: 25px;
  background: var(--lightBrown);
  border-radius: 50%;
}

.switch--left::after {
  background: var(--darkColor);
}

.switch--right {
  background: var(--darkColor);
}

.switch--right::after {
  top: auto;
  bottom: 25px;
}

.p {
  font-family: var(--fontStack);
  max-width: 700px;
  margin: 0 auto 20px;
  line-height: 1.5;
  font-size: 20px;
  color: var(--darkColor);
}

.p--l {
  margin: 20px 0 20px 0;
}

.code {
  display: inline-block;
  font-size: 18px;
  background: #000;
  color: #fff;
  padding: 0 24px;
  margin-bottom: 10px;
  border-radius: 15px;
}

.h1 {
  font-family: var(--fontStack);
  text-align: center;
  font-size: 48px;
  margin-bottom: 50px;
  color: var(--darkColor);
}

.link {
  display: block;
  margin: 20px 0;
}

.a,
.link,
.a:visited {
  font-size: 20px;
  color: var(--darkColor);
  font-family: var(--fontStack);
  color: var(--veryDarkColor);
}



